*{
  margin:0;
  padding:0;
  box-sizing:border-box;
}



:root{
  --mainColor: blue;
  --whiteColor: #fff;
  --blackColor: #000;
  --lightDark: #222;
}
body{
  font-family:'Lato', sans-serif;
}


/* Navbar Styles*/
nav{
  width:100%;
  height:70px;
  line-height:70px;
  padding: 0 100px;
  box-shadow:0 1px 3px rgba(0,0,0,.1);
  display:flex;
  position:fixed;
  top:0;
  background-color:#fff;
  z-index:9999;
}

.left{
  width:35%;
   text-align:left;
   color:var(--mainColor);
   }
   .center{
      width:300%;
       text-align:center;
       color:var(--lightDark);
       }
/* .left a{
  font-weight:700;
  color:var(--mainColor);
  font-size:21px;
}    */
   .right{
    width:50%;
    text-align:right;
    }   

nav ul{
  list-style:none;
}    

nav ul a{
  text-decoration:none;

}

.shoppingCart i{
  display: inline-block;
  font-size:20px;
   color:var(--lightDark);
  margin-right:15px;
}

 .cart{
  display:inline-block;
  width: 35px;
  height:35px;
  /* background:var(--mainColor); */
  color: var(--lightDark);
  border-radius:50%;
  line-height:35px;
  text-align:center;
  font-size:12px;

} 

/* Helpers */
.container{
  width:100%;
  padding: 0 100px;
}
/* Banner Styles*/
header{
  width:100%;
  background:url("./assets/background.jpg");
  height: 500px;
  margin-top:70px;
  background-size:cover;
  background-repeat:no-repeat;
  color:#fff;
    
}

.headertext{
  background: rgba(0,0,0,.5);
  width:100%;
  height:100%;
  display:flex;
  align-items:right;
  justify-content:right;
  
}

 .headertext h2{
  font-size:40px;
} 

 .headertext h1{
  margin-top:35px;
  font-size:60px; 

 }

 .headertext p{
  margin-top:10px;
  font-size:20px; 

 }



.btn{
  margin-top:35px;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #fff;
  border: 2px solid #3498db;
  text-decoration: none;
  background-color: #3498db;
}  
.products{
  display: flex;
  flex-wrap:wrap;
  width:100%;
}

.product{
  width:25%;
  padding: 0 12px;
  margin: 20px 0;
  overflow: hidden;
  position:relative;
}
.product-image{
  width:100%;
  height:200px;
  overflow: hidden;

 
}

.product-image1{
  display: flex;
  flex-wrap:wrap;
  width:100%;
  margin-top:30px;
  width:100%;
  height:300px;
  overflow: hidden;
  position:relative;
 
}
.product-details{
  padding: 15px 10px;
  text-align:center;
  background: var(--whiteColor);
  border-left: 1px solid #fafafa; 
  border-right: 1px solid #fafafa;
}

.product-name{
  font-size: 20px;
  font-weight:500;
  margin: 12px 0;

}

.product-price{
  font-size: 20px;
  font-weight:300;
  margin: 12px 0;
}

 .add-to-cart{
    background:var(--mainColor);
    padding:10px 20px;
    width:100%;
    color:var(--whiteColor);
    text-transform:uppercase;
    text-align:center;
    font-size:14px;
    cursor:pointer;
 }

/* not found style */
.not-found{
margin-top:100px;

}  
.home{
text-align: right;
} 

/* Cart Style*/
.cart-container{
width:100%;
margin-top:40px;
padding: 0 100px;
display:flex;
flex-wrap:wrap;
align-items: flex-start;
 }   

.cart-details{
  width:75%;
 border:1px solid #fafafa;
 border-radius:5px;
} 

.cart1{
  display:flex;
  align-items:center;
  background:var(--whiteColor);
  padding: 15px 20px;
}

.cart-image{
  display:inline-block;
  width:80%;
  height:80px;
  border-radius:5px;
  overflow:hidden;
     }

.cart-image img{
  width:100%;
  height:200%;

}  

.cart-product-name{
  width:150px;
  padding: 0 15px;
  font-weight:bold;
  font-size:14px;
  display: inline-block;

}

.cart-product-price{
 width: 120px;
 padding: 0 15px;
 display: inline-block;
 text-align: center;
 font-weight:bold;
 color:var(--mainColor);

}

.inc{
  display: inline-block;
  width:40px;
  height:40px;
  line-height:40px;
  text-align:center;
  border: 1px solid var(--mainColor);
  font-size:14px;
  color:var(--mainColor);
  border-radius: 3px;
  cursor:pointer;
}

.product-quantity{
  display:inline-block;
  padding:0 15px;
  font-size:15px;
  width:70px;
  text-align:center;

}

.dec{
 display: inline-block;
 width:40px;
 height:40px;
 line-height:40px;
 text-align:center;
 border: 1px solid rgb(187,58,58);
 font-size:14px;
 color:rgb(187,58,58);
 border-radius: 3px;
 cursor:pointer;
}

.product-total-price{
 display:inline-block;
 width:170px;
 padding:0 15px;
 text-align: center;
}

.delete-product{
 border:none;
 color:rgb(187,58,58);
 cursor:pointer;
}

/* footer */

.footer {
position: fixed;
left: 0;
bottom: 0;
width: 100%;
background-color: #fff;
color: var(--lightDark);
text-align: center;
border: 2px;
border-color: var(--lightDark); ;
}

/*cart summary style*/
.cart-summary{
width:25%;
padding: 0 0 0 20px;
margin-top:75px;
}

.summary{
background:var(--whiteColor);
padding:13px;
}

.summary h3{
font-size: 18px;
font-weight: normal;
border-bottom: 1px solid #999;
padding-bottom: 8px;
}
.total-items{
display:flex;
margin:15px 0;

}

.items{
width:50%;
text-align: left;
font-weight: normal;
}

.items-count{
  width:50%;
  text-align:right;
  font-weight: bold;
  color:var(--mainColor);

}

.total-price-section{
display:flex;
margin:15px 0;

}

.just-title{
width:50%;
text-align:left;
font-weight:normal;

}

.items-price{
width:50%;
text-align:right;
font-weight:bold;
color:var(--mainColor);
}


